@font-face {
  font-family: "fontello";
  src: url("../fonts/fontello/fontello.eot?81500498");
  src: url("../fonts/fontello/fontello.eot?81500498#iefix")
      format("embedded-opentype"),
    url("../fonts/fontello/fontello.woff2?81500498") format("woff2"),
    url("../fonts/fontello/fontello.woff?81500498") format("woff"),
    url("../fonts/fontello/fontello.ttf?81500498") format("truetype"),
    url("../fonts/fontello/fontello.svg?81500498#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
